import React from "react";

import Layout from "../../components/Layout";
import Helmet from "react-helmet";
import { Link, animateScroll as scroll } from "react-scroll";

import arrow from "../../img/arrow-icon.svg";
import budova from "../../img/budova.jpg";
import rybnik from "../../img/rybnik.jpg";
import hriste from "../../img/hriste.jpg";
import tricko from "../../img/tshirt-icon-orange.svg";
import hygiena from "../../img/hygiene-icon-orange.svg";
import karty from "../../img/cards-icon-orange.svg";

export default class Informace extends React.Component {
  render() {
    return (
      <Layout>
        <Helmet
          title={`Informace | Tbor Vysoina 3. turnus`}
          meta={[
            {
              name: `description`,
              content:
                "Informace o organizaci tbora Vysoina 3. turnus v Novm Mst na Morav. Popis lokality, zzem, prosted i seznam vc, co si maj astnci vzt s sebou.",
            },
            {
              name: "keywords",
              content:
                "Vysoina, Dtsk tbor, Nov Msto na Morav, 3. turnus, lokalita, prosted, zzem, vci s sebou, poteby",
            },
          ]}
        />
        <main className="telo-lide">
          <div className="dotazy">
            <h1>Nejastj dotazy</h1>
            <ul className="questions questions-one">
              <Link
                className="link-questions"
                to="arealFoto"
                smooth={true}
                duration={500}
              >
                <p>Kde dti bydl</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
              <Link
                className="link-questions"
                to="vybaveniFoto"
                smooth={true}
                duration={500}
              >
                <p>Co maj na tboe k dispozici</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
              <Link
                className="link-questions"
                to="okoliFoto"
                smooth={true}
                duration={500}
              >
                <p>Kam chod mimo tbor</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
            </ul>
            <ul className="questions questions-two">
              <Link
                className="link-questions"
                to="things-needed"
                smooth={true}
                duration={500}
              >
                <p>Co si vzt s sebou na tbor</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
              <Link
                className="link-questions"
                to="visits"
                smooth={true}
                duration={500}
              >
                <p>Mu dt navtvit</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
              <Link
                className="link-questions"
                to="post"
                smooth={true}
                duration={500}
              >
                <p>Posln poty a balk</p>
                <img
                  src={arrow}
                  alt="arrow icon"
                  width={20}
                  className="arrow-down"
                />
              </Link>
            </ul>
          </div>
          <div className="grid-informace">
            {/*<h1 class="kapitola">Zzem tbora</h1>*/}
            <div className="areal scroll-reveal-ambiente" id="areal">
              <h2>Arel</h2>
              <p>
                Dti jsou ubytovan v rekreanm arelu PR. Mlad tbornci
                bydl v hlavn budov, ti star v devnch chatkch. V hlavn
                budov je jdelna, toalety a sprchy. Do arelu krom zamstnanc
                nikdo ciz nesm.
              </p>
            </div>
            <div className="arealFoto scroll-reveal-ambiente" id="arealFoto">
              <img src={budova} alt="budova" />
            </div>
            <div className="vybaveni scroll-reveal-ambiente" id="vybaveni">
              <h2>Vybaven</h2>
              <p>
                Krom pjemnho a istho ubytovn jsou v arelu k dispozici
                hit, altn, sportovn vybaven, ohnit a dal spoleensk
                mstnosti. Pro len drobnch zrann a nemoc nm slou
                marodka v oddlen budov.
              </p>
            </div>
            <div
              className="vybaveniFoto scroll-reveal-ambiente"
              id="vybaveniFoto"
            >
              <img src={hriste} alt="hriste" />
            </div>
            <div className="okoli scroll-reveal-ambiente" id="okoli">
              <h2>Okol</h2>
              <p>
                Nejradji hrajeme hry v prod, a proto jsme rdi, e ns
                obklopuj krsn lesy, louky a vodn toky rskch vrch.
                Zrove se arel nachz i v blzkosti msta, do kterho v
                prbhu tbora dti rdy zavtaj.
              </p>
            </div>
            <div className="okoliFoto scroll-reveal-ambiente" id="okoliFoto">
              <img src={rybnik} alt="rybnik" />
            </div>
          </div>
          <div className="things-needed" id="things-needed">
            {/*<h2 class="thing-heading">Seznam vc</h2>*/}
            <div className="card clothes">
              <h2>Obleen a obuv</h2>
              <div className="card-content">
                <div className="card-img">
                  <img src={tricko} alt="obleceni ikona" width={200} />
                </div>
                <ul className="clothes-list">
                  <li>dostatek teplch i normlnch ponoek</li>
                  <li>dostatek spodnho prdla</li>
                  <li>trika s krtkm rukvem</li>
                  <li>trika s dlouhm rukvem</li>
                  <li>mikiny</li>
                  <li>vtrovku</li>
                  <li>krtk kalhoty (kraasy)</li>
                  <li>teplky (dlouh kalhoty)</li>
                  <li>plavky</li>
                  <li>obleen na span</li>
                  <li>obleen do msta</li>
                  <li>pokrvka hlavy</li>
                  <li>sportovn obuv</li>
                  <li>nepromokav boty (holnky)</li>
                  <li>pezvky</li>
                  <li>lahev na pit</li>
                </ul>
              </div>
            </div>
            <div className="card hygiene">
              <h2>Hygiena</h2>
              <div className="card-content">
                <div className="card-img">
                  <img src={hygiena} alt="hygiena ikona" width={200} />
                </div>
                <ul className="hygiene-list">
                  <li>runky</li>
                  <li>zubn kartek a pasta</li>
                  <li>mdlo</li>
                  <li>ampn (sprchov gel)</li>
                  <li>heben</li>
                  <li>opalovac krm</li>
                  <li>paprov kapesnky</li>
                </ul>
              </div>
            </div>
            <div className="card others">
              <h2>Ostatn</h2>
              <div className="card-content">
                <div className="card-img">
                  <img src={karty} alt="others ikona" width={200} />
                </div>
                <ul className="others-list">
                  <li>hry (nap. karty)</li>
                  <li>dopisn papry a oblky s potovn znmkou</li>
                  <li>psac poteby</li>
                  <li>repelent</li>
                  <li>vak na pinav prdlo</li>
                </ul>
              </div>
            </div>
          </div>
          <div className="visits-posts" id="visits">
            {/*<h2>Nvtvy a pota</h2>*/}
            <div className="section visits">
              <h3>Nvtvy</h3>
              <p>
                Aby mli vichni tbornci stejn podmnky a uili si neruen
                cel tbor, nvtvy do arelu nepovolujeme. Vdy ns (i dti)
                ale pot, kdy meme pedvat radost a pozdravy teba skrze
                poslan pohledy nebo men balky.
              </p>
            </div>
            <div className="section post" id="post">
              <h3>Pota</h3>
              <p>
                Do tbora nen problm poslat dopis, pohled nebo men balek.
                Adresu najdete v <a href="../kontakt/">kontaktech</a>. Ze
                zkuenosti vme, e pohledy dlaj radost i dosplm, a proto
                doporuujeme dtem pr pohled pedepsat, a tak mohou
                odpovdt.
              </p>
            </div>
          </div>
        </main>
      </Layout>
    );
  }
}
