import React from 'react'

import Layout from '../../components/Layout'
import Helmet from "react-helmet"
import Articles from '../../components/Articles'

export default class Aktuality extends React.Component {
  render() {
    return (
      <Layout>
        <Helmet
          title={`Aktuality | Tbor Vysoina 3. turnus`}
          meta={[
            {
              name: `description`,
              content: 'Aktuality organizan informace o dn na tboe Vysoina 3. turnus v Novm Mst na Morav',
            },
            {
              name: "keywords",
              content: 'Tbor, Vysoina, PR, Policie, Dtsk tbor, Nov Msto na Morav, 3. turnus, Aktuality, Informace, lnky',
            },
          ]} />
        <main className="telo">
          <Articles />
        </main>
      </Layout>
    )
  }
}