import React from "react";
import Helmet from "react-helmet"
import Articles from '../components/Articles'

import Layout from '../components/Layout'

import cover from "../img/leto2025cover.png";
import kamaradi from "../img/kamaradi_ikona.svg"
import catapult from "../img/catapult_ikona.svg"
import dobro from "../img/dobro_ikona.svg"

export const IndexPage = ({
}) => {

  return (
    <Layout>
      <Helmet
        title={`Domovsk strnka | Tbor Vysoina 3. turnus`}
        meta={[
          {
            name: `description`,
            content: 'Web Tboru Vysoina 3. turnus, kter je organizovann Polici esk republiky kad lto v Novm Mst na Morav',
          },
          {
            name: "keywords",
            content: 'Tbor, Vysoina, PR, Policie, Dtsk tbor, Nov Msto na Morav, 3. turnus',
          },
        ]}
      />
    <main className="telo-homepage">
      <div className="top-site">
        <div className="fotky">
          <div className="fotka">
          <img className="cover-photo" src={cover} alt="foto3" />
          </div>
        </div>
        <div className="info">
          <div className="text-homepage">
            <div className="text-heading">
              <h2>Zbava. Zitky.</h2>
              <h2>Vysoina</h2>
            </div>
            <p>Tbor Vysoina je policejn <strong>dtsk tbor</strong> s dlouholetou tradic, skvlou partou a hlavn <strong>zbavou</strong>. K dispozici mme 3 hit, hlavn budovu, chatky a <strong>krsnou produ.</strong></p>
          </div>
          <div class="scroll-downs">
            <div class="mousey">
              <div class="scroller"></div>
            </div>
          </div>
        </div>
      </div>
      <div className="bottom-site" id="bottom-site">
        <div className="boxes">
          <div className="box transition">
            <img alt="ikona kamaradu" src={kamaradi} width={100} />
            <div className="box-desc">
              <h2>Kamardi</h2>
              <p>Dti jsou rozdlen do <strong>dvch</strong> a <strong>kluich</strong> oddl. V oddlech si jsou vkov velmi blzko a brzy tak vznikaj ptelsk vztahy. Bhem CTH, <strong>diskotk</strong> a podobnch akc se navc dti <strong>rychle seznm</strong> i s dalmi tbornky.</p>
            </div>
          </div>
          <div className="box transition">
            <img alt="ikona her" src={catapult} width={100} />
            <div className="box-desc">
              <h2>Hry</h2>
              <p>V prbhu tbora hrajeme spoustu her v oddlech i v rmci celho tbora. Dti se tak mohou tit na tematickou <strong>celotborovou hru (CTH)</strong>, sportovn a mov hry, klasick <strong>tborov hry</strong> i non dobrodrun <strong>bojovky</strong>.</p>
            </div>
          </div>
          <div className="box transition">
            <img alt="ikona dobrodruzstvi" src={dobro} width={100} />
            <div className="box-desc">
              <h2>Dobrodrustv</h2>
              <p>To prav tborov dobrodrustv? Na Vysoin rozhodn. Nejlep letn zitky dti nezskvaj jen z her, ale ekaj na n teba akn <strong>policejn ukzky</strong>, zskvn a pouvn <strong>tborovch dovednost</strong> i celodenn <strong>vlet</strong>.</p>
            </div>
          </div>
        </div>
        <div className="news">
          <div className="news-heading">
            <h2>Aktuality</h2>
          </div>
          <div className="news-content">
            <Articles />
          </div>
        </div>
      </div>
    </main>
    </Layout>
  )
}

export default IndexPage
